import React from 'react';

const CommitInfoList = ({ commitInfoList, callback }) => {
    const handleButtonClick = (userName) => {
        callback(userName);
    };

  return (
    <>
        <h1 className="text-2xl font-bold text-white mb-4">  Github </h1>
        <div className=" shadow-md w-80 h-96 mt-4 overflow-auto">
            {!commitInfoList.length && <h3 className="text-1xl font-bold text-white mb-4">    </h3>}
            {commitInfoList.map((user, idx) => (
                <button
                    onClick={() => handleButtonClick(user.name)}
                    key={user.name} className="bg-gray-800 hover:bg-gray-700 p-4 rounded-lg flex flex-col mb-2">
                <p className="text-lg font-semibold text-white">{user.name}&nbsp;&nbsp;&nbsp;<span className="text-gray-300">{user.total}</span></p>
                </button>
            ))}
        </div>
    </>
  );
};

export default CommitInfoList;
